<template lang="jade">
	.dashboard
		section.rankings_unidades
			.header
				h1.logo
				h3.title Competição
				h2.title Treinamento Mateus
			
			.unidades
				//- transition-group(name="fade", class="list", v-on:leave="leave", v-on:enter="enter", duration="2500", v-show="index === show_unidades[0] || index === show_unidades[1]")
				transition-group(name="fade", class="list", v-on:leave="leave", v-on:enter="enter", duration="1000")
					.unidade(v-for="(unidade, index) in selectedUnidades", key="index", :data-index="index", v-show="showUnidade")
						ul.list
							li.place(:data-place="index", v-for="(user, index) in unidade.list")
								span.premio(v-if="index <= 0") R$ 150
								.content_infos
									.wrap_photo
										.photo(:style="{'background-image': 'url('+user.photo+')'}")

									.text
										span.desempenho {{user.score.toFixed()}} 
										span.name_user {{user.name}}
								span.position {{index + 1}}º
						
						h3.name_unidade {{unidade.name}}

		section.ranking_setores
			.header
				h2.title Valendo 
					strong R$1500,00

			ul.list
				li.place(v-for="(setor, index) in setores", :data-place="index")
					span.position {{index + 1}}º
					.content_infos
						span.desempenho {{setor.score.toFixed()}}
						span.name_user {{setor.name}}

					.team
						ul.list_team
							li.user(v-for="team in setor.members", :class="team.photo ? '' : 'no-photo'")
								.photo(:style="{'background-image': 'url('+team.photo+')'}")

			h3.name_setores Cohama

</template>

<script>
let axios = require('axios');
let data = require('./data.json');

let uniqueRandoms = [];
let numRandoms = 5;

export default {
	name: 'Dashboard',
	data () {
		return {
			showUnidade: true,
			show_unidades: [0,1],
			unidades: [],
			setores: []
		}
	},
	mounted(){
		let $this = this;
		
		axios.get('https://api.workeduc.com/gami/rank_panel/?campaign=mateus', { data: this.data })
			.then(function (response) {
				$this.unidades = response.data.unidades;
				$this.setores = response.data.setores;
			})
			.catch(function (error) {
				console.log(error.message);
			});
	},
	computed:{
		selectedUnidades(){
			return this.unidades.filter((unidade, index) => index === this.show_unidades[0] || index === this.show_unidades[1]); 
		}
	},
	created(){
		let $this = this;
		
		setInterval(function() {
			$this.goUnidade();
		}, 8000);
	},
	methods:{
		leave(el){

		},
		enter(el){

		},
		makeUniqueRandom(){
		    if (!uniqueRandoms.length) {
		        for (var i = 0; i < numRandoms; i++) {
		            uniqueRandoms.push(i);
		        }
		    }
		    var index = Math.floor(Math.random() * uniqueRandoms.length);
		    var val = uniqueRandoms[index];

		    uniqueRandoms.splice(index, 1);

		    return val;

		},
		goUnidade(){
			let $this = this;
			this.showUnidade = false;
			
			$this.$nextTick(function () {

				setTimeout(function() {
					$this.show_unidades = [$this.makeUniqueRandom(), $this.makeUniqueRandom()];
					$this.showUnidade = true;
				}, 400);
			})
		}
	}
}
</script>
<style lang="stylus" src="@/assets/stylus/main.styl"></style>


